@import url(https://fonts.googleapis.com/css?family=PT+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: PT Sans, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.Tooltip {
  position: absolute;
  z-index: 1;
}

.Tooltip__container {
  position: absolute;
  width: 150px;
  padding: 1rem;
  pointer-events: none;
}

.Tooltip__line {
  pointer-events: none;
  position: absolute;

  top: 0;
  bottom: 0;
  width: 1px;

  background: black;
}

.Diagram{
    position: relative;
}
